.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  font-size: 18px;
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header h1 {
  padding: 0;
}

.App-headline span:first-child {
  padding-right: 1.5rem;
}

.App-headline span:last-child {
  padding-left: 1.5rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
